.result-name-card {
    color: #e7e9f0;
    background: #282d4f;
    margin: 10px 15px;
    min-width: 75px;
    padding: 10px 20px;
    border-radius: 10px;
    display: flex;
    justify-content: center;
}

.result-name-card:hover {
    background: #4a5079;
}

.result-name {
    font-weight: 100;
}

.card-name {
    text-decoration: none;
}